import React from 'react';
import PropTypes from 'prop-types';
import { StaticQuery, graphql } from 'gatsby';

import Columns1Centered from '../Columns1Centered';
import MarkdownText from '../MarkdownText';
import Section from '../Section';

const Projects = ({ showPopupCard }) => (
  <StaticQuery
    query={graphql`
      query ProjectsQuery {
        projects: allMarkdownRemark(
          sort: { fields: frontmatter___date, order: DESC }
          filter: { fileAbsolutePath: { regex: "/projects/.*md$/" } }
        ) {
          edges {
            node {
              frontmatter {
                title
                short
              }
              html
            }
          }
        }
        tabProject: markdownRemark(
          fileAbsolutePath: { regex: "/projects.md$/" }
        ) {
          html
        }
      }
    `}
    render={data => (
      <Section title="Projects">
        <MarkdownText>{data.tabProject.html}</MarkdownText>
        <Columns1Centered cards={data.projects} showPopupCard={showPopupCard} />
      </Section>
    )}
  />
);

Projects.propTypes = {
  showPopupCard: PropTypes.func,
};

Projects.defaultProps = {
  showPopupCard: f => f,
};

export default Projects;
