import React from 'react';
import PropTypes from 'prop-types';

import CardSmall from './CardSmall';

const Columns2 = ({ cards, showPopupCard }) => (
  <div className="columns is-multiline">
    {cards.edges.map(({ node }, key) => {
      const dataPopupCard = {
        body: node.html,
        title: node.frontmatter.name,
      };

      return (
        <CardSmall
          iconName={node.frontmatter.icon}
          key={key}
          showPopupCard={showPopupCard}
          dataPopupCard={dataPopupCard}
          text={node.frontmatter.name}
        />
      );
    })}
  </div>
);

Columns2.propTypes = {
  cards: PropTypes.objectOf(PropTypes.array).isRequired,
  showPopupCard: PropTypes.func,
};

Columns2.defaultProps = {
  showPopupCard: f => f,
};

export default Columns2;
